.card-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
  max-height: 500px;
  max-width: 100%;
  margin: 25px;
  padding-top: 5px;
}

.product-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1920px;
  width: 1920px;
}

.with-button {
  display: flex;
  justify-content: space-between;
}
img {
  max-height: 500px;
  max-width: 260px;
}
