body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.link-name {
  color: black;
  text-decoration: none;
}

main {
  padding-top: 10px;
  display: flex;
  justify-content: stretch;
}

a {
  text-decoration: none;
  color: #fff;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-right: 50px;
  padding-left: 50px;
}
.card-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
  max-height: 500px;
  max-width: 100%;
  margin: 25px;
  padding-top: 5px;
}

.product-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1920px;
  width: 1920px;
}

.with-button {
  display: flex;
  justify-content: space-between;
}
img {
  max-height: 500px;
  max-width: 260px;
}

.product {
  margin: 25px;
  padding: 25px;
}

.product-content {
  display: flex;
  margin-bottom: 10px;
}

.product-content img {
  margin-right: 10px;
  max-width: 500px;
}


